import React, { useState, useEffect } from "react";
import "./PatientBuilder.css";
import { JsonViewer } from "@textea/json-viewer"; // updated import
import DetailsPopup from "../DetailsPopup/DetailsPopup";

// Global caching function to load patients & conditions once
let cachedPatientsAndConditions = null;
function getPatientsAndConditions() {
  if (cachedPatientsAndConditions)
    return Promise.resolve(cachedPatientsAndConditions);
  return fetch("/assets/patients_and_conditions.json")
    .then((response) => response.json())
    .then((data) => {
      cachedPatientsAndConditions = data;
      return data;
    });
}

const PatientBuilder = ({
  selectedPatient,
  selectedCondition,
  setSelectedPatient,
  setSelectedCondition,
  onNext,
  onBack,
}) => {
  const [patients, setPatients] = useState([]);
  const [conditions, setConditions] = useState([]);
  const [hoveredPatient, setHoveredPatient] = useState(null);
  const [isVideoLoading, setIsVideoLoading] = useState(false);

  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [popupJson, setPopupJson] = useState(null);
  const [isDetailsPopupOpen, setIsDetailsPopupOpen] = useState(false);


  useEffect(() => {
    getPatientsAndConditions()
      .then((data) => {
        setPatients(data.patients);
        setConditions(data.conditions);
      })
      .catch((error) =>
        console.error("Error fetching patients and conditions:", error)
      );
  }, []);

  useEffect(() => {
    if (
      selectedPatient &&
      selectedPatient.existing_condition !== "depression" &&
      selectedCondition === "Serotonin Syndrome"
    ) {
      setSelectedCondition(null);
    }
  }, [selectedPatient]);

  // When a new patient is selected, set the video to a loading state
  // to ensure the placeholder image is shown.
  useEffect(() => {
    if (selectedPatient) {
      setIsVideoLoading(true);
    }
  }, [selectedPatient]);

  const handleGo = () => {
    if (selectedPatient && selectedCondition) {
      onNext();
    }
  };

  const openPopup = (patient) => {
    if (patient && patient.fhirFile) {
      fetch(patient.fhirFile)
        .then((response) => response.json())
        .then((json) => {
          setPopupJson(json);
          setIsPopupOpen(true);
        })
        .catch((error) => console.error("Error fetching FHIR JSON:", error));
    }
  };

  const closePopup = () => {
    setIsPopupOpen(false);
    setPopupJson(null);
  };

  return (
    <div className="patient-builder-container">
      <div className="headerButtonsContainer">
      <button className="back-button" onClick={onBack}>
          <i className="material-icons back-button-icon">keyboard_arrow_left</i>
          Atrs
      </button>
      <button className="details-button" onClick={() => setIsDetailsPopupOpen(true)}>
          <i className="material-icons code-block-icon">code</i>&nbsp;
          Detalles sobre esta Demo
      </button>
      </div>
      <div className="frame">
        <div className="selection-section">
          <div className="header2">Seleccionar un Paciente</div>
          <div className="patient-list">
            {patients.map((patient) => {
              const isSelected = selectedPatient && selectedPatient.id === patient.id;
              return (
                <div
                  key={patient.id}
                  className="patient-card"
                >
                  <div
                    className={`patient-video-container ${isSelected ? "selected" : ""}`}
                    onClick={() => setSelectedPatient(patient)}
                  >
                    <img
                      src={patient.img}
                      className="patient-img"
                      alt={patient.name}
                      draggable="false"
                      onDragStart={(e) => e.preventDefault()}
                      style={{ opacity: isSelected && !isVideoLoading ? 0 : 1 }}
                    />
                    {isSelected && (
                      <video
                        key={patient.id}
                        src={patient.video}
                        className="patient-video"
                        autoPlay
                        muted
                        loop
                        onCanPlay={() => setIsVideoLoading(false)}
                        style={{ opacity: isVideoLoading ? 0 : 1 }}
                      />
                    )}
                    <div className="ehr-label" onClick={(e) => { e.stopPropagation(); openPopup(patient); }}>
                      Registro de Salud Sinttico (FHIR)
                    </div>
                  </div>
                  <div className="patient-info">
                    <div className="category-value">
                      {patient.name}, {patient.age} aos, {patient.gender}
                    </div>
                    <div className="category-value">
                      Condicin existente: {patient.existing_condition}
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        <div className="selection-section">
          <div className="header2">Explorar una Condicin</div>
          <div className="lighttext">
          En esta demostracin, una persona, simulada usando Gemini 2.5 Flash, interactuar con un agente de IA, construido con MedGemma. 
          Ni la persona simulada ni el agente de IA han recibido el diagnstico para la condicin actual (seleccionada abajo). 
          El agente de IA facilita la recopilacin estructurada de informacin, diseada para recopilar y resumir tilmente los sntomas del paciente. 
          Para los propsitos de esta demostracin, el agente de IA tambin tiene acceso a elementos del registro de salud del paciente (proporcionado como recursos FHIR).
          </div>
          <div className="condition-list">
            {conditions.map((cond) => {
              const isDisabled =
                cond.name === "Sndrome de Serotonina" &&
                selectedPatient &&
                selectedPatient.existing_condition !== "Depression";
              return (
                <div
                  key={cond.name}
                  className={`condition-card lighttext ${
                    selectedCondition === cond.name ? "selected" : ""
                  } ${isDisabled ? "disabled" : ""}`}
                  onClick={
                    !isDisabled
                      ? () => setSelectedCondition(cond.name)
                      : undefined
                  }
                >
                  <div><strong>{cond.name}</strong></div>
                  <div>{cond.description}</div>
                </div>
              );
            })}
          </div>
        </div>
        <button
          className="info-button"
          onClick={handleGo}
          disabled={!(selectedPatient && selectedCondition)}
        >
          Lanzar simulacin
        </button>
      </div>
      {isPopupOpen && (
        <div className="popup-overlay" onClick={closePopup}>
          <div
            className="popup-content json-popup-content"
            onClick={(e) => e.stopPropagation()}
          >
            <h2>Registro Electrnico de Salud Sinttico</h2>
            <span>Esta es una muestra del registro electrnico de salud del paciente, mostrado en formato estndar (FHIR). Este registro FHIR, como el paciente, fue generado nicamente para los propsitos de esta demo.</span>
            <div className="json-viewer-container">
              <JsonViewer value={popupJson} theme="monokai" />
            </div>
            <button className="popup-button" onClick={closePopup}>
              Cerrar
            </button>
          </div>
        </div>
      )}
       <DetailsPopup
        isOpen={isDetailsPopupOpen}
        onClose={() => setIsDetailsPopupOpen(false)}
      />
    </div>
  );
};

export default PatientBuilder;
